<template>
<div id="search">
    <div class="searchHaerd">
        <input type="text" v-model="searchValue"  class="searchInout">
        <span @click="stepAction" class="step">取消</span>
    </div>
    <ul class="list" v-show="listFalg">
        <li class="itme" v-for="itme in list" :key="itme">{{itme}}</li>
    </ul>
    <div class="searchSuggestions" v-show="!listFalg">
        <h3>热门搜索</h3>
        <div class="itme">
            <a class="color">七夕礼物</a>
            <a>天然空调 水牛皮席</a>
            <a class="color">9.9元超值专区</a>
            <a>钻石煎锅低至99元</a>
            <a>拖鞋</a>
            <a>反季特惠</a>
            <a>男士内裤</a>
            <a>冷感夏凉被</a>
            <a>女鞋</a>
            <a>猫粮</a>
            <a>咖啡</a>
            <a>床垫</a>
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios';
export default {
    data:function(){
        return {
            searchValue:'',
            list:[],
            listFalg:false,
        }
    },
    methods:{
        stepAction(){
            this.$router.go(-1);
        }
    },
    watch:{
        searchValue:function(){
            if(this.searchValue == ''){
                this.listFalg = false;
                return false;
            }
            this.listFalg = true;
            let params = new URLSearchParams();
            params.append("keywordPrefix",this.searchValue,);
            axios.post('xhr/search/searchAutoComplete.json',params)
            .then((response)=> {
                this.list = response.data.data;
            })
            .catch((error) =>{
                console.log(error);
            });
        }
    }
}
</script>

<style lang="scss" scoped>

#search{
    width: 100%;
    background-color: #eee;
    position: fixed;
    top:0;
    bottom: 0;
    left: 0;
    z-index: 10;
    font-size: 14px;
    .searchHaerd{
        padding: 9px 15px;
        display: flex;
        background: #fff;
        overflow: hidden;
        .searchInout{
            flex: 1;
            height: 26px;
            border: none;
            outline: none; 
            padding-left: 32px;
            background: #eee;
            background-image: url('//yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/search2-553dba3aff.png');
            background-repeat: no-repeat;
            background-size: 14px;
            background-position: 7px 7px;
        }
        .step{
            display: inline-block;
            margin-top: 2px;
            width: 12vw;
            text-align: center;
            
        }
    }
    .list{
        background: #fff;
        padding:  0 15px;
        .itme{
            line-height: 50px;
            border-bottom: 1px solid #eee;
        }
    }
    .searchSuggestions{
        background: #fff;
        padding:  0 15px;
        h3{
            color: #999;
            line-height: 40px;
        }
        .itme a{
            display: inline-block;
            padding: 2px 7px;
            border: 1px solid #999;
            color: #333;
            font-size: 12px;
            margin: 0 15px 15px 0;
        }
        .itme .color{
            border-color: #b4282d;
            color: #b4282d;
        }
    }
    
}



</style>

